
* {
  margin: 0;
  padding: 0;
}
table {
  width: 980px;
  background: #dcdcff;
  margin: 0 auto;
  border-collapse: collapse;
}
table thead {
  background: #6f6fff;
  padding: 8px;
  border: 1px solid red;
}
table thead tr {
  height: 30px;
}
table .goods span {
  float: left;
  margin-top: 46px;
}
table .goods img {
  width: 100px;
  height: 100px;
  background: pink;
  vertical-align: middle;
  margin-left: 50px;
  margin-right: 20px;
  margin-top: 5px;
  margin-bottom: 5px;
  float: left;
  box-sizing: border-box;
}
table .goods-number input {
  width: 60px;
  text-align: center;
}
table .goods-number span {
  cursor: pointer;
}
.foot {
  width: 980px;
  margin: 0 auto;
  background: pink;
  height: 60px;
  line-height: 60px;
}
.foot .fl {
  float: left;
}
.foot .fr {
  float: right;
}
.foot .closing {
  background: grey;
  height: 60px;
  width: 60px;
  line-height: 60px;
}
.foot .selected {
  margin-right: 8px;
}



