
.tmp {
  position: relative;
}
.tmp .border-dashed {
  border: dashed 1px #FF6878;
}
.tmp .img-bg {
  width: 100%;
  vertical-align: top;
}
.tmp .tmp-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.tmp .tmp-content .top-bg {
  height: 50%;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #F8F9FA;
  box-sizing: border-box;
  overflow: hidden;
}
.tmp .tmp-content .top-bg .custom {
  width: 100%;
}
.tmp .tmp-content .top-bg .btn-picture {
  margin-right: 10%;
}
.tmp .tmp-content .top-bg .btn-picture,
.tmp .tmp-content .top-bg .btn-my-pictures {
  width: 32%;
}
.tmp1 {
  position: relative;
  height: 50%;
  font-size: 0.14rem;
  color: #FF6878;
}
.tmp1 .text-to {
  position: absolute;
  top: 12%;
  left: 10%;
}
.tmp1 .text-recipient {
  position: absolute;
  top: 12%;
  left: 18%;
}
.tmp1 .text-content {
  position: absolute;
  display: block;
  top: 12%;
  left: 58%;
  width: 30%;
  word-break: break-all;
  text-align: left;
  line-height: 1.5;
}
.tmp1 .text-from-msgs {
  position: absolute;
  bottom: 16%;
  left: 58%;
}
.tmp1 .text-from-msgs .text-from {
  color: #ddd;
  display: inline-block;
  margin-left: 0.06rem;
}
.tmp1 .text-phone {
  color: #ddd;
  position: absolute;
  bottom: 5%;
  right: 3%;
  font-size: 0.12rem;
}
.tmp2 {
