
html {
  font-size: 100px;
}
input,
button {
  outline: none;
}
.clear:after {
  content: '';
  display: block;
  clear: both;
}
.hidden {
  display: none !important;
}
.fl {
  float: left !important;
}
.fr {
  float: right !important;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #A0AAB2;
  background: #F8F9FA;
  font-size: 0.12rem;
}



