
.photoWill {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: white;
}
.photoWill h4 {
  margin: 0;
  padding: 0.14rem;
  font-size: 0.14rem;
  text-align: center;
  background: black;
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  top: 0;
  left: 0;
  z-index: 2;
}
.photoWill h4 .fa {
  float: left;
  position: relative;
  top: 0.02rem;
  left: .02rem;
}
.photoWill .imgs {
  margin-top: 0.42rem;
}
.photoWill .imgs:after {
  content: '';
  display: block;
  clear: both;
}
.photoWill .imgs > div {
  float: left;
  width: 33.33333%;
  position: relative;
  overflow: hidden;
}
.photoWill .imgs > div img.bg {
  width: 100%;
  position: relative;
  vertical-align: top;
}
.photoWill .imgs > div img {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
.photoWill .imgs > div.active:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: solid red 4px;
}
.photoWill .bottom {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.85);
  text-align: center;
  padding: 0.10rem;
}
.photoWill .bottom button {
  padding: 0.06rem;
  color: white;
  border: solid 0.01rem transparent;
  background: #FF6878;
  border-radius: 0.04rem;
  width: 1rem;
  cursor: pointer;
}
.photoWill .bottom button:disabled {
  background: #888;
}



